<style lang="less" scoped>
.praise-container{
    display: flex;
    justify-content: flex-start;
    padding: 0 30px;
    box-sizing: border-box;
    width: 100%;
    height: auto;
    margin-top: 10px;
    & > div{
        margin: 10px 0;
        &:nth-child(1){
            width: 9%;
            text-align: center;
            & > img{
                width: 30px;
                height: 30px;
            }
        }
        &:nth-child(2){
            width: 91%;
            word-wrap: break-word;
            word-break: normal;
            & > span{
                position: relative;
                height: 40px;
                line-height: 40px;
                font-size: 25px;
                width: auto;
                color: #333;
                &:last-child.last-praise-person{
                    color: #527dad;
                    margin-left: 5px;
                }
            }
        }
    }
}
</style>

<template>
    <div class="praise-container">
        <div>
            <img src="../assets/img/wdzy_diazan.png">
        </div>
        <div class="praise-list">
            <span @click="enterWorkDetail" v-for="(praiseItem, index) in praiseObject.praiseList" :key="index">{{praiseItem.nickName}}
                <i v-if="index !== praiseObject.praiseList.length -1">、</i>
            </span>
            <span  @click="enterWorkDetail" class="last-praise-person" v-if="praiseObject && praiseObject.praiseList.length >= 1">等{{praiseObject.praiseNumber}}人点赞</span>
        </div>
    </div>
</template>
<script>
export default {
    props: [
        'praiseObject'
    ],
    methods: {
        // 进入作业详情页面
        enterWorkDetail() {
            this.$router.push(`/workRangeDetail/${this.praiseObject.taskId || this.$route.params.taskId}/${this.praiseObject.jobcompletionId}?type=praise`)
        },
    }
}
</script>



