<style lang="less" scoped>
@import url('../assets/css/comment.less');
</style>
<style>
.all-emoji-list{
	margin-left: 25px;
}
</style>

<template>
	<div class="comment-container animated" @touchmove.prevent>
		<div @click="showCommentModal" class="comment-bg"></div>
			<div class="comment-wrapper">
				<div>
					<span class="comment-close-modal" @click="showCommentModal">×</span>
				</div>
				<textarea name="commentModal" :placeholder="placeholder" rows="7" cols="35" autofocus ref="textAreaRef" id="commentModal" maxlength="150" v-model="textareaData"></textarea>
				<div class="comment-footer">
					<div><span >{{fontNum}}</span>/150</div>
					<div><span @click="showEmojiModal" class="emoji-bg"></span><span @click="publishCommentContent">发布</span></div>
				</div>
			</div>
			<emoji-com v-if="showEmojiStatus" @getEmoji="getEmoji"></emoji-com>
	</div>
</template>
<script>
import emojiCom from '@/components/emoji'
export default {
	props: [
		'workDetail',
	],
	components: {
		emojiCom
	},
	data () {
		return {
			textareaData: '',
			showEmojiStatus: false,
			commentReg: /^[\s]*$/,
			placeholder: '写评论: '
		}
	},
	mounted () {
		this.refs.textAreaRef.focus()	
	},
	computed: {
		fontNum(){
			if(150 - this.textareaData.length > 0){
				return this.textareaData.length
			}else{
				this.textareaData = this.textareaData.substr(0, 150)
				return 150
			}
		},
		LOGIN_USER_OBJECT_JOIN(){
			return this.$store.state.userModule.LOGIN_USER_OBJECT.roleList
		},
		LOGIN_USER_OBJECT(){
			return this.$store.state.userModule.LOGIN_USER_OBJECT
		}
	},
	mounted () {
		if(this.LOGIN_USER_OBJECT.user.userId !== this.workDetail.userId){
			this.workDetail.nickName ? this.placeholder = `回复${this.workDetail.nickName}: `  : ''
		}
	},
	methods: {
		showCommentModal(){
			this.$emit('showCommentModal', {
                commentStatus: false
            })
			this.showEmojiStatus = false
		},
		showEmojiModal(){
			this.showEmojiStatus = !this.showEmojiStatus
		},
		getEmoji(data){
			this.textareaData += data 
		},
		publishCommentContent(){

			if(this.commentReg.test(this.textareaData)){
				this.$Toast({
					message: "请输入内容",
					position: "middle",
					duration: 1000
				})
				return
			}

			if(this.workDetail.doCommentStatus){

				this.workDetail.doCommentStatus = false

				this.$Indicator.open({
					text: "评论中...",
					spinnerType: "fading-circle"
				})

				let params = {
					jobcompletionId: this.workDetail.jobcompletionId,
					content: this.textareaData
				}

				if(this.workDetail.commentId){
					params['commentId'] = this.workDetail.commentId
				}

				this.axios.post("/api/saveJobsComment", params).then(data => {
					this.workDetail.doCommentStatus = true
					this.$Indicator.close()
					if (data.data.status === "success") {

						let userInfo = {
							userId: data.data.result.userId,
							avatar: data.data.result.user.avatar.startsWith('http') ? data.data.result.user.avatar : `${this.productURL}/${data.data.result.user.avatar}`,
							commentTime: data.data.result.commentTime,
							content: data.data.result.commentContent,
							jobcompletionId: data.data.result.jobcompletionId,
							nickName: data.data.result.user.nickname,
							commentType: "TEXT",
							commentId: data.data.result.jobcommentId,
							userRoleList: this.LOGIN_USER_OBJECT_JOIN,
							commentStatus: true,
							index: this.workDetail.index
						}
							
						if(!this.workDetail.commentId){
							this.$emit('showCommentModal', userInfo)
						}else{
							let params = {
								comment: userInfo,
								parentComment: {
									content: this.textareaData,
									userRoleList: this.workDetail.userRoleList,
									nickName: this.workDetail.nickName,
									userId: this.workDetail.userId
								}
							}
							this.$emit('showCommentModal', params)
						}
						this.$Toast({
							message: "评论成功",
							position: "middle",
							duration: 1000
						})
						

						let routers = this.$route.fullPath
          				this.$store.commit('cleanAboutAndMine', routers)
					} else {
						this.$Toast({
							message: data.data.result,
							position: "middle",
							duration: 1000
						})
					}

				}).catch(err => {
					console.log(err)
				})
			}

		}
	}
}
</script>



