<style lang="less" scoped>
.container{
  background-color: rgba(0,0,0,.3);
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 999;
  top: 0;
  max-width: 1040px;
}
.select-container{
  background-image: url('../assets/img/bj2.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  width: 640px;
  height: 100vh;
  position: fixed;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  z-index: 999;
  &>.select-account-login{
    border: none;
    border: 0;
    outline: none;
    width: 200px;
    height: 70px;
    color: #fff;
    background-color: #44b336;
    text-align: center;
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    font-size: 20px;
    border-radius: 10px;
    bottom: 350px;
    display: inline-block;
  }
  &>p{
    background-image: url('../assets/img/zh.png');
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    width: 400px;
    height: 150px;
    text-align: center;
    color: #44b336;
    margin-top: 200px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 400px;
    padding-top: 50px;
    font-size: 26px;
  }
  &>.select-account-list{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 600px;
    height: 240px;
    overflow-x: hidden;
    overflow-y: auto;
    width: 300px;
    overflow-x: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &>div{
      height: 50px;
      &>img{
        width: 50px; 
      }
      &>*{
        vertical-align: middle;
      }
    }
    &>.opacity-unselect{
      opacity: .3;
      font-size: 25px;
    }
    &>.opacity-select{
      opacity: 1;
      font-size: 25px;
    }
  }
}
</style>

<template>
  <div class="container"  @touchmove.prevent v-if="showMutiAccount">
    <section class="select-container">
      <p>该手机号下包含多个账号<br>请选择其一进行登录</p>
      <div class="select-account-list">
        <div v-if="MULTI_USER_OBJECT && MULTI_USER_OBJECT.length > 1" v-for="(item, index) in MULTI_USER_OBJECT" :key="index" @click="selectCurrent(item)"  :class="{'opacity-unselect': 1, 'opacity-select': item.userId === currentSelect.userId}" >
          <img v-if="item.type === 'qq'" src="../assets/img/zh_qq2.png" alt="QQ">
          <img v-if="item.type === 'wechat'" src="../assets/img/zh_qq2.png" alt="wechat">
          <span>{{item.account}}</span>
          <span>{{item.category}}</span>
        </div>
      </div>
      <button @click="selectLoginEvent" class="select-account-login">登录</button>
    </section>
  </div>
</template>
<script>
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  export default {
    data () {
      return {
        currentSelect: '',
        showMutiAccount: 0
      }
    },
    methods: {
      ...mapActions([
        'SELECT_MUTIACCOUNT'
      ]),
      selectCurrent(item){
        this.currentSelect = item
      },
      selectLoginEvent(){
        this.SELECT_MUTIACCOUNT({
          params: {
            userId: this.currentSelect.userId,
            subject: this.currentSelect.subject
          }
        }).then(data =>{
          if(data.data.content.user && data.data.token){
            this.$emit('selectAccount', 0)
            this.$Toast({
              message: '登录成功',
              position: 'middle',
              duration: 500
            })
            localStorage.setItem('dapeng', data.data.token)
          }
        }).catch(err =>{
          console.log(err)
        })
      }
    },
    computed: mapState([
      'MULTI_USER_OBJECT'
    ]),
    watch: {
      MULTI_USER_OBJECT(state){
        
      }
    },
    mounted () {
      this.showMutiAccount = 1
    }
  }
</script>



