<style lang="less" scoped>
.instrcutorStyle{
    color: #44b336;
}
.studentStyle{
    color: #527dad;
}
.comment-container{
    width: 100%;
    & > div{
        display: flex;
        justify-content: flex-start;
        padding: 0 20px;
        box-sizing: border-box;
        width: 100%;
        div{
            &:nth-child(1){
            width: 10%;
            text-align: center;
                & > img{
                    width: 30px;
                    height: 30px;
                }
            }
            &:nth-child(2){
                width: 90%;
                margin: 5px 0;
                line-height: 1.4;
                & > span.comment-audio{
                    position: relative;
                    & > img{
                        height: 40px;
                        vertical-align: middle;
                    }
                    & > i{
                        position: absolute;
                        top: 50%;
                        right: -50px;
                        transform: translateY(-50%);
                        color: #44b336;
                    }
                }
            }
        }
    }
}
@media screen and (min-width: 1000px) {
    .comment-type-list{
        margin: 0;
        width: 100%;
        color: #527dad;
        padding: 10px 0 20px 11.5%;
        box-sizing: border-box;
    } 
}
@media screen and (max-width: 800px) {
    .comment-type-list{
        margin: 0;
        width: 100%;
        color: #527dad;
        padding: 10px 0 20px 90px;
        box-sizing: border-box;
    } 
}
.instrcutorStyle,.studentStyle,.emoji-style,.comment-type-list{
    font-size: 25px;
}
i{
    font-style: normal;
}
</style>
<style>
.emoji-style{
    color: #4e4e4e;
}
.emoji-style > img{
    width: 40px;
    height: 40px;
    vertical-align: middle;
}
.emoji-style > span > img{
    width: 40px;
    height: 40px;
    vertical-align: middle;
}
.replayed-content{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    word-wrap: break-word;
    word-break: break-all;
    overflow : hidden;
    text-overflow: ellipsis;
}
</style>

<template>
    <div class="comment-container">
        <div v-for="(commentItem, index) in commentObject.comment.commentList" :key="index">
            <div><img v-if="index === 0" src="../assets/img/wdzy_pinglun.png"></div>
            <div class="replayed-content">
                <span  
                    @click="showCommentModal(commentItem)" 
                    :class="commentItem.comment.userRoleList.join('').toLowerCase().includes('instructor') ? 'instrcutorStyle' : 'studentStyle' ">
                    {{commentItem.comment.nickName}}
                    <i v-if="!commentItem.parentComment.userId">：</i>
                </span>
                <span  
                    @click="showCommentModal(commentItem)" 
                    class="emoji-style" v-if="commentItem.comment.commentType === 'TEXT' && !commentItem.parentComment.userId" 
                    v-html="convertEmotions(commentItem.comment.content)">
                </span>
                <span @click="showCommentModal(commentItem)"
                      class="emoji-style"
                      v-if="commentItem.comment.commentType === 'TEXT' && commentItem.parentComment.userId">
                    <span>回复</span>
                    <span :class="commentItem.parentComment && commentItem.parentComment.userRoleList && commentItem.parentComment.userRoleList.includes('instructor')  ? 'instrcutorStyle' : 'studentStyle' ">{{commentItem.parentComment.nickName}}</span>
                    <span v-html="convertEmotions(commentItem.comment.content)"></span>
                </span>
                <!-- 语音播放和暂停状态管理 -->
                <span @click="playAudio(commentItem, index)" class="comment-audio" v-if="commentItem.comment.commentType.includes('AUDIO')"><audio :src="commentItem.comment.content"></audio>
                    <img v-if="commentItem.comment.commentType === 'AUDIO_PAUSE' || !play_flag" :src="pauseImg" alt="语音">
                    <img v-if=" commentItem.comment.commentType === 'AUDIO_PLAY' && play_flag" :src="playImg" alt="语音">
                    <!-- <img v-if="!play_flag" :src="pauseImg" alt="语音">
                    <img v-if="play_flag" :src="playImg" alt="语音"> -->
                    <i>{{commentItem.comment.audioLength}}"</i>
                </span>       
            </div>
            
        </div>
        <p class="comment-type-list" @click="enterWorkDetail">有{{commentObject.comment.commentNum}}人<span v-if="commentObject.comment.commentNum > 1">都</span>在评论</p>
        <transition name="fade" mode="out-in">
            <work-range-list-comment @showCommentModal="showCommentModal" v-if="showListComment"  :workDetail="workDetail"></work-range-list-comment>
        </transition>
        <login key="login" v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
    </div>
</template>
<script>
let audioObject = new Audio()
import workRangeListComment from '@/components/workRangeListComment'
import login from '@/components/login'
export default {
    props: [
        'commentObject'
    ],
    data () {
        return {
            workDetail: {
                jobcompletionId: null,
                doCommentStatus: null,
                index: 0,
                commentId: ''
            },
            showListComment: false,
            showLoginModal: 0,
            // audioObject: new Audio(),
            play_flag: false,
            timer:null,
            timer2:null,
            Time:0,
            pauseImg: require('../assets/img/yuwoxiangguan_yuyin.png'),
            playImg: require('../assets/img/sound.gif')
        }
    },
    components: {
        workRangeListComment,
        login
    },
    computed: {
        LOGIN_USER_OBJECT() {
            return this.$store.state.userModule.LOGIN_USER_OBJECT
        }
    },
    mounted () {
        this.commentObject.areaList.forEach((ele, index_) => {
            ele.commentList.forEach((element, indexs) => {
                if(element.comment.commentType === 'AUDIO'){
                    element.comment.commentType = 'AUDIO_PAUSE'
                }
            })
        })
    },
    methods: {
        enterWorkDetail(){
            // 进入作业详情页面
            this.$router.push(`/workRangeDetail/${this.commentObject.comment.taskId || this.$route.params.taskId}/${this.commentObject.comment.jobcompletionId}?type=comment`)
        },
        playAudio(item, index){
            clearInterval(this.timer2)
            //console.log( this.commentObject.areaList[6].commentList[0].comment.audioLength)
            //console.log(this.commentObject.areaList[6].commentList[0].comment.commentType) 
            this.commentObject.areaList.forEach((element, index) => {
                element.commentList.forEach((element_, index_) => {
                    if(element_.comment.commentType.includes('AUDIO')){ 
                        element_.comment.commentType = 'AUDIO_PAUSE'
                    }
                })
            })
            
            if(audioObject && item.comment.content === audioObject.src){
                if(audioObject.paused){
                    item.comment.commentType = 'AUDIO_PLAY'
                    audioObject.play()
                    this.play_flag=true
                    if(this.Time <= 0){
                        this.Time = item.comment.audioLength+1
                    }
                }else{
                    item.comment.commentType = 'AUDIO_PAUSE'
                    audioObject.pause()
                    this.play_flag=false
                }
            }else{
                audioObject.src = item.comment.content
                item.comment.commentType = 'AUDIO_PLAY'
                audioObject.play()
                this.play_flag=true
                this.Time=item.comment.audioLength+1
                //clearTimeout(this.timer)
            }
            if(item.comment.commentType = 'AUDIO_PLAY'){
                // this.timer=setTimeout(() => {
                //     item.comment.commentType = 'AUDIO_PAUSE'
                //     this.play_flag=false
                // }, item.comment.audioLength * 1024)
                var _this=this
                if(this.play_flag){
                    this.timer2 = setInterval(()=>{
                        _this.Time--
                        //console.log(_this.Time)
                        if(_this.Time<=0){
                            item.comment.commentType = 'AUDIO_PAUSE'
                            this.play_flag=false
                            clearInterval(this.timer2)
                        }
                    },1000)
                }
                
            }
            
        },
        showDialogEvent(){
            this.showLoginModal = 0
        },
        // 显示评论弹出层
        showCommentModal(comment){

            if(!this.LOGIN_USER_OBJECT){
                this.showLoginModal = 1
                return
            }

            if(this.showListComment){
                if(comment.commentStatus){
                    this.showListComment = false
                    this.commentObject.comment.commentList.unshift({ comment, parentComment: {} })
                }else{
                    this.showListComment = false
                }

                if(comment.hasOwnProperty('comment') && comment.comment.hasOwnProperty('commentStatus') && comment.hasOwnProperty('parentComment')){
                    this.commentObject.comment.commentList.unshift(comment)
                    this.showListComment = false
                }
            }else{
                this.showListComment = true
                let parmas = {
                    jobcompletionId: comment.comment.jobcompletionId,
                    doCommentStatus: true,
                    index: this.commentObject.index,
                    userRoleList: comment.comment.userRoleList,
                    nickName: comment.comment.nickName,
                    userId: comment.comment.userId
                }
                
                if(comment.parentComment.userId){
                    this.workDetail = parmas
                }else{
                    parmas['commentId'] = comment.comment.commentId
                    this.workDetail = parmas
                }
            }
        }
    }
}
</script>



