<style lang="less" scoped>
  .bg_icon {
    background-image: url('../assets/img/tongyi.png');
    background-repeat: no-repeat;
    background-size: cover;
  }
  .back_img {
    position: absolute;
    left: 13%;
    top: 10%;
    width: 30px;
    height: auto;
  }
  .modal {
    background-color: rgba(0, 0, 0, .4);
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100000;
    text-align: center;
    margin: 0 auto;
    -webkit-backdrop-filter: blur(10px);
    overflow: hidden;
    height: 100vh;
    width: 100%;
    max-width: 40rem;
    max-width: 1040px;
  }
  .modal>div {
    width: 100%;
    height: 100%;
    position: relative;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 5%;
    padding-top: .3125rem;
    max-width: 1040px;
  }
  .modal>div>h5 {
    font-weight: normal;
  }
  .modal>div>h3,
  .modal>div>h5 {
    margin-top: 6.25rem;
  }
  .modal>div>img:nth-child(1) {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 640px;
    height: auto;
    z-index: -1;
    overflow: hidden;
  }
  .login_username {
    margin: 30px 0;
    &:nth-child(1){
      margin-top: 50px;
    }
  }
  .close_img {
    width: 70px;
    position: absolute;
    top: 84px;
    right: 55px;
  }
  .login_username>img,
  .login_password>img {
    width: 50px;
    height: 50px;
    vertical-align: middle;
  }
  .login_username>span>input,
  .login_password>span>input {
    height: 50px;
    outline: none;
    text-indent: 0.5em;
    border: 2px solid #999;
    border-radius: 0;
    margin-left: 10px;
    vertical-align: middle;
    border-color: transparent transparent #999 transparent;
    background-color: transparent;
    font-size: 30px;
    width: 300px;
  }
  .login_username>span>input::-webkit-input-placeholder{
    font-size: 30px;
  }
  .login_password>span>input::-webkit-input-placeholder{
    font-size: 30px;
  }
  .reg_unremember {
    width: 80%;
    text-align: center;
    margin: 40px auto;
    text-indent: 1em;
    font-size: 25px;
    &>span {
      font-size: 25px;
      vertical-align: middle;
    }
    &>span>a {
      color: #44b336;
      font-size: 25px;
      vertical-align: middle;
    }
    &>span>b {
      vertical-align: middle;
      display: inline-block;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      border: 3px solid #e2e2e2;
    }
  }
  .login_unremember {
    margin: 40px 0;
    &>span{
      font-size: 30px;
    }
  }
  .login_unremember>span:nth-child(2) {
    margin-left: 210px;
  }
  .login_unremember>span:nth-child(2)>a {
    color: #44b336;
  }
  .login_button>button {
    border: none;
    outline: none;
    border-radius: .3125rem;
    background-color: #44b336;
    color: #fff;
    padding: 20px 90px;
    font-size: 30px;
    cursor: pointer;
  }
  .fade-enter {
    opacity: 0;
  }
  .fade-enter-active {
    transition: opacity .3s;
  }
  .fade-leave {
    opacity: 1;
  }
  .fade-leave-active {
    opacity: 0;
    transition: opacity .3s;
  }
  .show_name>span {
    vertical-align: middle;
    color: #fff;
    font-size: 1.25rem;
    display: inline-block;
    width: 4rem;
    white-space: nowrap;
  }
  .show_name>span:nth-child(1) {
    &>img {
      vertical-align: middle;
      width: 3rem;
      height: 1.25rem;
    }
    &>span:nth-child(1) {
      font-size: 1rem;
    }
  }
  .show_name {
    position: relative;
    width: 60%;
    &>img {
      margin: 0 !important;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .change-tap-btn{
    margin-top: 350px;
    &>button{
      background-color: transparent;
      width: 220px;
      height: 60px;
      border: none;
      border: 0;
      color: #000;
      outline: none;
      font-size: 30px;
    }
    &>button.active{
      background-image: url('../assets/img/xxk.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
      color: #44b336;
    }
  }
  .loginFormDisplay{
    display: none;
  }
  .valid-code-form-tip{
    display: inline-block;
    background-color: #eaeaea;
    width: 470px;
    height: 70px;
    border-radius: 25px;
    font-size: 25px;
    text-align: left;
    padding: 15px;
  }
  #sendCode-valid-code{
    width: 200px;
  }
  .getValidCode{
    background-color: transparent;
    color: #44b336;
    display: inline-block;
    width: 190px;
    height: 60px;
    border: none;
    border: 0;
    outline: none;
    font-size: 30px;
    padding: 0;
  }
  #username-valid-code{
    width: 390px;
  }
  .reg-username{
    margin-top: 420px;
  }
</style>

<template>
  <section class="login-position-fixed">
    <transition-group enter-active-class="zoomIn" tag="div" leave-active-class="zoomOut">
      <login-valid key="loginValid" class="animated" v-if="!LOGIN_USER_OBJECT && loginValidStatus" :mobileNum="mobileNum" v-on:closeValidEvent="closeValidEvent" v-on:backLoginEvent="backLoginEvent"></login-valid>
      <forgetPwd key="forgetPwd" v-if="showForget" class="animated" v-on:forgetEvent="forgetEvent"></forgetPwd>
      <select-account class="animated" key="selectAccountModal" @selectAccount="selectAccount" v-if="MULTI_USER_OBJECT && MULTI_USER_OBJECT.length > 1 && selectAccountStatus"></select-account>
    </transition-group>
    <!-- 注册 -->
    <div id="regModal" @touchmove.prevent class="modal" v-if="showLoginModal">
      <div>
        <img src="../assets/img/bj.png" alt="背景图片">
        <img src="../assets/img/guanbi.png" alt="关闭" class="close_img" @click="closeLoginModal">
        <img src="../assets/img/dl_jian_lh.png" class="back_img" @click="backImgevent" alt="返回登录窗口">
        <div class="login_username reg-username">
          <img src="../assets/img/phone.png" alt="用户注册">
          <span><input type="text" @blur="blur" autocomplete="off" placeholder="请输入手机号" v-model="reg_params.phone" id="reusername" class="reusername" name="reusername"></span>
        </div>
        <div class="login_password">
          <img src="../assets/img/pw.png" alt="用户密码">
          <span><input type="password" v-on:blur="blur" autocomplete="off" placeholder="请输入密码" v-model="reg_params.password" id="repassword" class="repassword" name="repassword"></span>
        </div>
        <div class="reg_unremember">
          <span @click="changeBgIcon"><b :class="{bg_icon:bg_icon}"></b> 同意并愿意遵守<a >《用户协议》</a>和<a>《隐私政策》</a></span>
        </div>
        <div class="login_button" @click="regBtn"><button>注册</button></div>
      </div>
    </div>
    <!-- 登录 -->
    <div id="loginModal" @touchmove.prevent class="modal" v-if="showLoginModal">
      <div>
        <img src="../assets/img/bj.png" alt="背景图片">
        <img src="../assets/img/guanbi.png" alt="关闭" class="close_img" @click="closeLoginModal">
        <p class="change-tap-btn">
          <button @click="accountLoginEvent" :class="{active: loginFormDisplay}">用户名登录</button>
          <button @click="validLoginEvent" :class="{active: !loginFormDisplay}">验证码登录</button>
        </p>

        <section :class="{'account-form':1, loginFormDisplay: !loginFormDisplay}">
          <div class="login_username">
            <img src="../assets/img/gr.png" alt="用户登录">
            <span><input type="text" v-on:blur="blur" autocomplete="off" placeholder="手机/QQ/微信" v-model="params.phone" id="username" class="username" name="username"></span>
          </div>
          <div class="login_password">
            <img src="../assets/img/pw.png" alt="用户密码">
            <span><input type="password" v-on:blur="blur" @focus="inputPasswordEvent" ref="inputPassword" autocomplete="off" placeholder="请输入密码" v-model="params.password" id="password" class="password" name="password"></span>
          </div>
          <div class="login_unremember">
            <span><a @click="forgetPwd">忘记密码?</a></span><span><a @click="quikSign">快速注册</a></span>
          </div>
          <div class="login_button" @click="btnLogin"><button>登录</button></div>
        </section>

        <section :class="{'valid-code-form': 1, loginFormDisplay: loginFormDisplay}">
          <span class="valid-code-form-tip">只有绑定过的手机号方可使用验证码登录<br>否则请使用用户名登录</span>
          <div class="login_username">
            <img src="../assets/img/gr.png" alt="用户登录">
            <span><input type="text" v-on:blur="blur" autocomplete="off" placeholder="请输入手机" v-model="params.phoneCode" id="username-valid-code" class="username-valid" name="username"></span>
          </div>
          <div class="login_password">
            <img src="../assets/img/yzm.png" alt="验证码">
            <span><input type="text" v-on:blur="blur" autocomplete="off" placeholder="请输入验证码" v-model="params.sendCode" id="sendCode-valid-code" class="sendCode-valid" name="sendCode"></span>
            <button @click="getValidCode" class="getValidCode" :disabled="validCodeDisabled">{{getValidateCode}}</button>
          </div>
          <div class="login_unremember">
            <span><a @click="forgetPwd">忘记密码?</a></span><span><a @click="quikSign">快速注册</a></span>
          </div>
          <div class="login_button" @click="loginBtnByCode"><button>登录</button></div>
        </section>
      </div>
    </div>
  </section>
</template>
<script>
  import $ from 'jquery'
  import loginValid from '@/components/loginValid'
  import selectAccount from '@/components/selectAccount'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  export default {
    components: {
      loginValid,
      selectAccount
    },
    computed: mapState([
      'LOGIN_USER_OBJECT',
      'MULTI_USER_OBJECT'
    ]),
    watch: {
      LOGIN_USER_OBJECT(state){
        if(state){
          this.loginValidStatus = 0
        }
      }
    },
    data() {
      return {
        loginModal: false,
        closeModal: 0,
        params: {
          phone: null,
          password: null,
          qq: null,
          type: 'mobile',
          value: null,
          phoneCode: null,
          sendCode: null
        },
        reg_params: {
          phone: '',
          password: '',
          sourceType: 'WAP'
        },
        regModal: false,
        bg_icon: 0,
        bg_icon_count: 0,
        allow_statement: 0,
        showForget: 0,
        showLoginModal: 1,
        loginFormDisplay: 1, // 显示,
        loginValidStatus: 0, // 显示验证真实手机Modal状态
        mobileNum: '',
        selectAccountStatus: 0,
        getValidateCode: '获取验证码',
        validCodeDisabled: false, // 是否可点击
        countDown: 60,
        wechatReg: /^[a-zA-Z]([-_a-zA-Z0-9]{5,19})+$/
      }
    },
    mounted() {
      
    },
    methods: {
      ...mapActions([
        'LOGIN_USER',
        'MODAL_REGISTER',
        'GET_MOBILE_CODE',
        'GET_COMPARE_MOBILECODE'
      ]),
      // 选择多线索
      selectAccount(data){
        this.selectAccountStatus = data
      },
      // 返回登录
      backLoginEvent(info){
        this.loginValidStatus = info
        this.showLoginModal = 1
      },
      // 关闭登录验证Modal
      closeValidEvent(info){
        this.loginValidStatus = info
        this.showLoginModal = 1
      },
      // 键盘弹起
      inputPasswordEvent(){
        
      },
      loginBtnByCode(){
        if(!this.params.phoneCode){
          this.$Toast({
            message: '请填写手机号码',
            position: 'middle',
            duration: 1000
          })
          return
        }
        this.GET_COMPARE_MOBILECODE({
          comparePasswd: this.params.sendCode,
          comparedPasswd: localStorage.getItem('sendCode')
        }).then(data => {
          if(data.data && data.data.status === 0){
            this.$Toast({
              message: '验证码失效, 请重新获取',
              position: 'middle',
              duration: 1000
            })
          }else if(data.data.token){
            localStorage.removeItem('sendCode')
            localStorage.setItem('dapeng', data.data.token)
            this.$emit('showDialogEvent', 0)
            this.$store.commit('cleanAllStatus')
            this.$Toast({
              message: '登录成功',
              position: 'middle',
              duration: 500
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      getValidCode(){
        if(!this.params.phoneCode){
          this.$Toast({
            message: '请填写手机号码',
            position: 'middle',
            duration: 1000
          })
          return
        }
        let params = {
          phone: this.params.phoneCode,
          type: 'LOGIN_PHONE'
        }
        
        this.GET_MOBILE_CODE( params ).then(data => {

          this.validCodeDisabled = true
          let timer = setInterval(()=>{
            this.countDown--
            this.getValidateCode = this.countDown
            if(this.countDown === 0){
              this.validCodeDisabled = false
              this.getValidateCode = '获取验证码'
              this.countDown = 60
              clearInterval(timer)
            }
          }, 1000)

          if(data.data.status === 'failure'){
            clearInterval(timer)
            this.validCodeDisabled = false
            this.$Toast({
              message: data.data.result,
              position: 'middle',
              duration: 1000
            })
          }
          if(data.data.status === 1){
            localStorage.setItem('sendCode', data.data.content)
          }else if(data.data.status === -1){
            clearInterval(timer)
            this.validCodeDisabled = false
            this.$Toast({
              message: data.data.content,
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          clearInterval(timer)
          this.validCodeDisabled = false
          console.log(err)
        })
      },
      // 显示账号登录框
      accountLoginEvent(){
        this.loginFormDisplay = 1
      },
      // 显示验证码登录框
      validLoginEvent(){
        this.loginFormDisplay = 0
      },
      // 忘记密码
      forgetPwd() {
        this.$router.push({
          name: 'forgetPwdByPhone'
        })
      },
      forgetEvent() {
        this.showForget = 0;
        this.showLoginModal = 1;
        this.$emit('showDialogEvent', 0);
        this.params.phone = null;
        this.params.password = null;
      },
      regBtn() {
        // 判断验证规则
        if (this.reg_params.phone.length === 0) {
          this.$Toast({
            message: '手机号不能为空',
            position: 'middle',
            duration: 1000
          })
          return;
        } else if (this.reg_params.password.length === 0) {
          this.$Toast({
            message: '密码不能为空',
            position: 'middle',
            duration: 1000
          })
          return;
        } else if (this.allow_statement === 0) {
          this.$Toast({
            message: '请同意注册协议后操作',
            position: 'middle',
            duration: 1000
          })
          return;
        } else if (!(/^1[3456789]\d{9}$/.test(this.reg_params.phone))) {
          this.$Toast({
            message: '手机号格式不正确,请重新填写',
            position: 'middle',
            duration: 1000
          })
          return
        }
        this.$Indicator.open({
          text: '注册中...',
          spinnerType: 'fading-circle'
        })
        // 注册Actions
        this.MODAL_REGISTER( this.reg_params ).then(data => {
          if(data.data.status === 'failure'){
            this.$Toast({
              message: data.data.result,
              position: 'middle',
              duration: 1000
            })
            this.$Indicator.close()
            return
          }
          if(data.data.token){
            this.$Indicator.close()
            localStorage.setItem('dapeng', data.data.token)
            this.$store.commit('cleanAllStatus')
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 切换注册一级登录
      backImgevent() {
        $('#loginModal').slideDown(300);
        $('#regModal').slideUp(300);
      },
      changeBgIcon() {
        this.bg_icon_count++
        if (this.bg_icon_count % 2 == 1) {
          this.bg_icon = 1
          this.allow_statement = 1
        } else {
          this.allow_statement = 0
          this.bg_icon = 0
        }
      },
      quikSign() {
        $('#loginModal').slideUp(300);
        $('#regModal').slideDown(300);
      },
      btnLogin() {
        if (!this.params.phone || this.params.phone == ' ') {
          this.$Toast({
            message: '账号不能为空',
            position: 'middle',
            duration: 1000
          })
          return
        } else if (!this.params.password || this.params.password == ' ') {
          this.$Toast({
            message: '密码不能为空',
            position: 'middle',
            duration: 1000
          })
          return
        }else if(this.params.password.length <6){
          this.$Toast({
            message: '请输入至少6位数的密码',
            position: 'middle',
            duration: 1000
          })
          return
        }
        if (this.params.phone.length != 11) {
          if(this.wechatReg.test(this.params.phone)){
            this.params.type = 'wechat'
          }else{
            this.params.type = 'qq'
          }
        }else{
          this.params.type = 'mobile'
        }
        this.params.value = this.params.phone

        this.$Indicator.open({
          text: '登录中...',
          spinnerType: 'fading-circle'
        })

        this.LOGIN_USER(this.params).then(data => {
          this.$Indicator.close()
          if(data.content && data.status === 0){
            this.$Toast({
              message: data.content,
              position: 'middle',
              duration: 1000
            })
            return
          }else if(data.data.content.result.user.clueCategory !== '-1' || (data.data.content.result.user.clueCategory === '-1' && (data.data.content.result.user.isAdministrator === 'Y' || data.data.content.result.user.isInstructor === 'Y'))){

            this.$Indicator.close()

            this.$Toast({
              message: '登录成功',
              position: 'middle',
              duration: 500
            })
            
            localStorage.setItem('dapeng', data.data.token)
            this.$store.commit('cleanAllStatus')
          }else if(data.data.length > 1){
            this.selectAccountStatus = 1
            this.showLoginModal = 0
          }else{
            this.mobileNum = this.params.value
            this.showLoginModal = 0
            this.loginValidStatus = 1
          }
        })
      },
      blur() {
        var u = navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
      },
      closeLoginModal() {
        this.$emit('showDialogEvent', 0);
        this.params.phone = '';
        this.params.password = '';
      },
    },
  }
</script>



