<style lang="less" scoped>
@import url("../assets/css/workArea.less");
.as-post_ceontent_list_M {
  max-height: 58vw;
  overflow: hidden;
}
.as-post_ceontent_list_PC {
  max-height: 410px;
  overflow: hidden;
}
.as-img_content>img {
  height: 8rem !important;
  width: auto!important;
  max-width: 100%;
}
.as-imgs_content {
  height: 200px;
  overflow: hidden;
  text-align: center
}
.as-defaultWidth {
  
}
.as-defaultHeight {
 height: 100%
}
.post_item_footer  > * ,.post_item > span, .parise-event{
  cursor: pointer;
}
</style>

<template>
    <div>
        <div class="recomment-container">
          <div class="recommend" id="recommend" v-for="(item, index) in areaList" :key="index">
              <div class="recommend_item">
                <div class="avatar">
                    <img  @click="__enterUserInfoCenter(item)" :src="item.avatar.indexOf('http') > -1 ? item.avatar : productURL + item.avatar" alt="用户头像">
                    <a  @click="__enterUserInfoCenter(item)" class="ava_name">{{item.nickName}}</a>
                    <img @click="__enterUserInfoCenter(item)" v-if="item.recommend === 'Y'" class="avatar-recommend" :src="item.recommend === 'Y' ? commendImg  : '' " alt="推荐">
                    <img @click="__enterUserInfoCenter(item)" class="avatar-recommend" v-if="item.level === '90'" src="../assets/img/zuoyequ_icon_you.png" alt="成绩">
                    <img @click="__enterUserInfoCenter(item)" class="avatar-recommend" v-if="item.level === '80'" src="../assets/img/zuoyequ_icon_liang.png" alt="成绩">
                    <img @click="__enterUserInfoCenter(item)" class="avatar-recommend" v-if="item.level === '70'" src="../assets/img/zuoyequ_icon_zhong.png" alt="成绩">
                    <img @click="__enterUserInfoCenter(item)" class="avatar-recommend" v-if="item.level === '60'" src="../assets/img/zuoyequ_icon_cha.png" alt="成绩">
                    <br>
                    <a @click="__enterUserInfoCenter(item)" class="time">{{new Date(item.submit) | monthFm}}</a>
                    <span @click="enterWorkRe(item)" class="task-title">来自<span>{{item.taskTitle}}</span></span>
                </div>

                <div class="post_item">
                  <span @click="delJobcompletionById(item, index)" v-if="item.statu === 'UNAPPROVED' && item.level === '0'"><img src="../assets/img/wodezuoye_trash.png" alt="删除"></span>

                  <span @click="delJobcompletionById(item, index)" class="coreClass trash" v-if="item.level === '60' && item.statu === 'UNAPPROVED'"><img src="../assets/img/wodezuoye_trash.png" alt="成绩"></span>
                  <span @click="delJobcompletionById(item, index)" class="coreClass trash" v-if="item.level === '70' && item.statu === 'UNAPPROVED'"><img src="../assets/img/wodezuoye_trash.png" alt="成绩"></span>
                  <span @click="delJobcompletionById(item, index)" class="coreClass trash" v-if="item.level === '80' && item.statu === 'UNAPPROVED'"><img src="../assets/img/wodezuoye_trash.png" alt="成绩"></span>
                  <span @click="delJobcompletionById(item, index)" class="coreClass trash" v-if="item.level === '90' && item.statu === 'UNAPPROVED'"><img src="../assets/img/wodezuoye_trash.png" alt="成绩"></span>

                </div>
                
              </div>
              <div class="post_content">
                <p class="post_ceontent_list_emp" @click="enterWorkDetail($event, item, index)">{{item.jobContent}}</p>
                  <!-- ------------M- PhotoSwipe插件-------------- -->
                  <Photoswipe v-if="device">
                    <ul class="post_ceontent_list as-post_ceontent_list_M" @click="enterWorkDetail($event, item, index)">
                      <span class="imgsNumIcon" @click="enterWorkDetail($event, item, index)" v-if="item.imgSmall.split(',').length > 6">{{item.imgSmall.split(',').length - 6}}+</span>
                      <li class="imgs_content" v-if="item.img.split(',').length > 1" v-for="(src, index) in item.img.split(',')"
                          :data-pswp-src="src"
                          :key="`bg-${index}`"
                          :style="getImageItemStyle(src)"
                          v-lazy:background-image="src">
                      </li>
                      <li class="img_content"
                          v-if="item.imgSmall.split(',').length === 1" 
                          v-for="(imgItem, index) in item.imgSmall.split(',')" :key="imgItem">
                        <img
                            :class="item.width >= item.height ? 'defaultWidth' : 'defaultHeight'"
                            :key="index"
                            :src="imgItem"
                            :data-pswp-src="imgItem"
                            v-lazy="imgItem"
                        />
                      </li>
                    </ul>
                  </Photoswipe>
                  <!-- ------------PC- v-viewer插件-------------- -->
                  <viewer v-if="!device">
                    <!-- <img v-for="(src) in item.img.split(',')" :src="src" :key="src"> -->
                    <ul class="post_ceontent_list as-post_ceontent_list_PC" @click="enterWorkDetail($event, item, index)">
                      
                      <span class="imgsNumIcon" @click="enterWorkDetail($event, item, index)" v-if="item.imgSmall.split(',').length > 6">{{item.imgSmall.split(',').length - 6}}+</span>
                      <li class="imgs_content as-imgs_content" v-if="item.img.split(',').length > 1" v-for="(src, index) in item.img.split(',')"
                          :key="index">
                          <img
                            class="as-defaultHeight"
                            :key="index"
                            :src="src"
                        />
                        {{src.width}}
                      </li>
                      <li class="img_content as-img_content"
                          v-if="item.imgSmall.split(',').length === 1" 
                          v-for="(imgItem, index) in item.imgSmall.split(',')" :key="imgItem">
                        <img
                            :class="item.width >= item.height ? 'defaultWidth' : 'defaultHeight'"
                            :key="index"
                            :src="imgItem"
                        />
                      </li>
                    </ul>
                  </viewer>
                  <!-- ------------自定义添加新功能版本-------------- -->
                  <!-- <div class="as-swiper">
                    <ul class="post_ceontent_list" @click="enterWorkDetail($event, item, index)">
                      <span class="imgsNumIcon" @click="enterWorkDetail($event, item, index)" v-if="item.imgSmall.split(',').length > 6">{{item.imgSmall.split(',').length - 6}}+</span>
                      <li class="imgs_content" v-if="item.img.split(',').length > 1 && index < 6" v-for="(src, index) in item.img.split(',')"
                          :key="`bg-${index}`"
                          :style="getImageItemStyle(src)"
                          v-lazy:background-image="src"
                          @click="enterPhotoDetail(item,index)">
                          
                      </li>
                      <li class="img_content as-img_content"
                          v-if="item.imgSmall.split(',').length === 1" 
                          v-for="(imgItem, index) in item.imgSmall.split(',')" :key="imgItem">
                        <img
                            :class="item.width >= item.height ? 'defaultWidth' : 'defaultHeight'"
                            :key="index"
                             @click="enterPhotoDetail(item,index)"
                            :src="imgItem"
                        />
                      </li>
                    </ul>
                    <previewer v-if="showPreViewer" :previewerArr='previewerArr' @closePreview="closePreview" :isActive="isActive"></previewer>
                  </div> -->
               </div>
              <!-- 评论作品 START -->
              <div class="post_item_footer">
                <span class="post_ceontent_list_emp" @click="enterWorkDetail($event, item, index)" v-if="item.categoryName === '设计学院'" style="background-color: #a9e4ff;color: #1c79ed">#{{item.categoryName}}</span>
                <span class="post_ceontent_list_emp" @click="enterWorkDetail($event, item, index)" v-if="item.categoryName === '美术学院'">#{{item.categoryName}}</span>
                <span class="post_ceontent_list_emp" @click="enterWorkDetail($event, item, index)" v-if="!item.categoryName" style="background-color: #fff"></span>
                <span class="post_ceontent_list_emp" @click="showCommentModal(item, index)">
                    <img src="../assets/img/zuoyexiangqing_pinglun.png" alt="评论"><span>{{item.commentNum}}</span>
                </span>
                <span class="parise-event" @click="__parise(item)">
                    <img alt="点赞" :src="item.isPraise == 'Y' ? allFollow : circleFollow"><span>{{item.praiseNumber}}</span>
                </span>
              </div>
              <work-range-parise-list v-if="item.praiseList.length > 0" :praiseObject="item"></work-range-parise-list>
              <work-range-comment @unshiftComment="unshiftComment" v-if="item.commentList.length > 0"  :commentObject="{ comment: item, index: index, areaList: areaList}"></work-range-comment>
          </div>
        </div>

        <transition-group enter-active-class="fadeIn" leave-active-class="fadeOut">
          <login key="login" v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
          <work-range-list-comment @showCommentModal="showCommentModal" key="commentList" v-if="showListComment" :workDetail="workDetail"></work-range-list-comment>
        </transition-group>
    </div>
</template>
<script>
import Vue from "vue"
import VueLazyload from "vue-lazyload"
import login from '@/components/login'
import { device, isIOS } from "../utils/device"
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers(
  "workRangeModule"
);
import previewer from '@/components/previewer2'
import workRangePariseList from '@/components/workRangePariseList'
import workRangeComment from '@/components/workRangeComment'
import workRangeListComment from '@/components/workRangeListComment'
Vue.use(VueLazyload, {
  error: require("../assets/img/default_blue.png"),
  loading: require("../assets/img/Loading4.gif")
})

export default {
  props: ["areaList"],
  components: {
    login,
    previewer,
    workRangePariseList,
    workRangeComment,
    workRangeListComment
  },
  data() {
    return {
      images: ['https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1654937742,1393289912&fm=27&gp=0.jpg'],


      Index:0,
      getIndex: 0,
      // 未点赞
      circleFollow: require("../assets/img/zuoyexiangqing_dianzan.png"),
      // 点赞
      allFollow: require("../assets/img/shouye_dainzan_select.png"),
      showLoginModal: 0,
      showPreViewer: false,
      isActive:0,
      previewerArr: [],
      commendImg: require('../assets/img/zuoyequ_icon_tuijian.png'),
      showListComment: false,
      workDetail: {
        jobcompletionId: null,
        doCommentStatus: null,
        index: 0
      },
    }
  },
  computed: {
    LOGIN_USER_OBJECT() {
      return this.$store.state.userModule.LOGIN_USER_OBJECT
    },
    device() {
      return device;
    },
    isIOS(){
      return isIOS
    }
  },
  methods: {
    show () {
      const vuer = this.$el.querySelector('.images').$vuer
      vuer.show()
    },
    //点击进入详情页
    enterPhotoDetail(item,index) {
      this.isActive=index
      this.showPreViewer = true
      this.previewerArr=[]
      item.imgSmall.split(',').map((n,m)=>{
        this.previewerArr.push(n)
      })
    },

    getImageItemStyle(src) {
      return {
          backgroundImage: `url(${src})`,
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
      }
    },
    enterWorkRe(workInfo){
      this.$router.push(
        {
          name: 'workRe',
          params: {
            taskId: workInfo.taskId,
            title: workInfo.taskTitle
          }
        }
      )
    },
    unshiftComment(params){
      this.areaList.forEach((ele, index) => {
        if(index === params.comment.index){
          ele.commentList.unshift(params)
        }
      })
    },
    // 显示评论弹出层
    showCommentModal(commentObj, index){
      if(!this.LOGIN_USER_OBJECT){
        this.showLoginModal = 1
        return
      }
      if(commentObj.hasOwnProperty('commentStatus')){
        if(commentObj.commentStatus){
          this.showListComment = false
          this.areaList.forEach((ele, index_) => {
            if(index_ === commentObj.index){
              ++ele.commentNum
              ele.commentList.unshift(
                {
                  comment: commentObj,
                  parentComment: {}
                }
              )
            }
          })
        }else{
          this.showListComment = false 
        }
      }else{
        this.showListComment = true
        this.workDetail = {
          jobcompletionId: commentObj.jobcompletionId,
          doCommentStatus: true,
          index: index
        }
      }
    },
    // 删除作业
    delJobcompletionById(item, index){
      this.$MessageBox.confirm('确定要删除作业?', '提示')
      this.$MessageBox.confirm('确定要删除作业?').then(action => {
        this.axios.post('/api/delJobcompletionById', {
          jobcompletionId: item.jobcompletionId
        }).then(data => {
          // console.log(data)
          if(data.data === '删除成功'){
            this.$Toast({
              message: data.data,
              position: 'middle',
              duration: 1000
            })
            this.areaList.splice(index,1)

          }else if(data.data === '作业批改中'){
            this.$Toast({
              message: data.data,
              position: 'middle',
              duration: 1000
            })
          }else{
            this.$Toast({
              message: data.data,
              position: 'middle',
              duration: 1000
            })
          }
          this.$Indicator.close()

          let routers = this.$route.fullPath
          this.$store.commit('cleanAboutAndMine', routers)
        }).catch(err => {
          console.log(err)
        })
      }).catch(err => {
        console.log(err)
      })
      
    },
    closePreview(){
      this.showPreViewer = false
    },
    showPreviewerEvent(arr, index){

      this.getIndex = index
      this.$store.commit('modifyPreviewerPosition', window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0)
      this.showPreViewer = true
      this.previewerArr = arr
    },
    ...mapActions([
      'WORK_PARISE'
    ]),
    // 子级组件自定义事件
    showDialogEvent(data) {
      this.showLoginModal = data
    },
    // 进入用户中心页面【获取点击的用户信息，根据用户ID进入用户中心页面】
    __enterUserInfoCenter(item) {
      this.$router.push({
        name: "perWorkCircle",
        params: {
          id: item.userId
        }
      })
    },
    // 进入作业详情页面
    enterWorkDetail($event, item, index) {
      // 判断当前点击的元素对象, 通过className进行匹配, 若点击元素className包含[post_ceontent_list]则进入作业详情页面, 反之则打开图片预览器
      if($event.target.className.indexOf('post_ceontent_list') !== -1){
        let router = this.$route.fullPath
        let type = ''
        if(router.includes('highestHeat')){
          type = 'hot'
        }else if(router.includes('latestTime')){
          type = 'new'
        }else if(router.includes('myWorkInRange')){
          type = 'mine'
        }
        this.$router.push(`/workRangeDetail/${item.taskId || this.$route.params.taskId}/${item.jobcompletionId}?type=${type}&index=${index}`)
      }
    },
    // 点赞操作【获取当前将要点击作品的点击状态，根据点击状态请求不同的接口，未登录则不允许操作】
    __parise(item) {
      if (!this.LOGIN_USER_OBJECT) {
        this.showLoginModal = 1
        return
      }

      if(item.debounce){
        this.$set(item, 'debounce', false)
        let data = this.WORK_PARISE({
          jobcompletionId: item.jobcompletionId,
          action: item.isPraise === "Y" ? "NORMAL" : "UPVOTE"
        }).then(data => {
          this.$set(item, 'debounce', true)
          if(data.data.status === 'Y'){
            this.$set(item, "praiseNumber", ++item.praiseNumber)
            let praiseObject = {
              avatar: this.LOGIN_USER_OBJECT.user.avatar,
              nickName: this.LOGIN_USER_OBJECT.user.nickname,
              userId: this.LOGIN_USER_OBJECT.user.userId
            }
            item.praiseList.unshift(praiseObject)
            this.$Toast({
              message: "点赞成功",
              position: "middle",
              duration: 1000
            })
          }else{
            this.$set(item, "praiseNumber", --item.praiseNumber)
            item.praiseList.forEach((ele, index) => {
              if(ele.userId === this.LOGIN_USER_OBJECT.user.userId){
                item.praiseList.splice(index, 1)
              }
            })
            this.$Toast({
              message: "取消点赞",
              position: "middle",
              duration: 1000
            })
          }
          this.$set(item, "isPraise", data.data.status)

          let routers = this.$route.fullPath
            
          this.$store.commit('cleanAboutAndMine', routers)

        }).catch(err => {
          this.$set(item, "praiseNumber", --item.praiseNumber)
          console.log(err)
        })
      }
    },
    rotate() {

      var oDiv = document.getElementsByClassName('pswp__top-bar')[0]
      oDiv.innerHTML += '<button title="Toggle fullscreen" class="pswp__button pswp__button--rotate" ></button>'
      var Rotate = document.getElementsByClassName('pswp__button--rotate')[0]
      Rotate.onclick = function() {
        var Arr=[]

        var Pic = document.getElementsByClassName('pswp__img')
        for(var i=0;i<Pic.length;i++){
          //Pic[i].style.display = 'block'
          if(Pic[i].className == 'pswp__img pswp__img--placeholder'){

          }else {
            Arr.push(Pic[i])
          }
        }
        Arr[1].style.transform='rotate(90deg)'
    }
      console.log(oDiv)
    }
  },
  watch: {
    areaList(data){
      data.forEach(ele => {
        if(!ele.hasOwnProperty('debounce')){
          ele['debounce'] = true
        }
      })
    },

  },
  created() {
   
  },
  mounted () {
    // console.log('mounted')
    // this.rotate()
    //预览页面添加新功能
    // this.Index = document.getElementsByClassName('pswp__counter')[0].innerHTML.split('/')[0]-1
    // console.log(this.Index)
    // 节流目的
    this.areaList.forEach(element => {
      element['debounce'] = true
    })
    this.$nextTick(() => {
      if (this.device) {
        let _this = this;
        this.previewerObj = new Swiper("#previewer", {
          observer:true, // 修改swiper自己或子元素时，自动初始化swiper
          observeParents:true, // 修改swiper的父元素时，自动初始化swiper
          on: {
            slideChangeTransitionEnd: function(){
              // _this.mobileImgIndex = this.activeIndex
            }
          }
        })
      } else {
        this.viewImg = this.previewerArr[this.imgIndex]
      }
    })
  }
}
</script>



