let agent = navigator.userAgent

let device = /(phone|pad|pod|iPhone|iPod|ios|iPad|android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i.test(agent)
let isIOS = !!agent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)

export {
    device,
    isIOS
} 


