<style lang="less" scoped>
/* 底部导航 TAB START */
  footer {
    position: fixed;
    display: -webkit-flex;
    display: flex;
    bottom: 0;
    width: 100%;
    background-color: #fff;
    min-height: 100px;
    z-index: 10;
  }
  @media screen and (min-width: 1040px){
    footer{
      width: 1040px;
    }
  }
  .flex_bottom {
    flex: 1;
    position: relative;
    text-align: center;
    cursor: pointer;
    &>img{
      width: 50px;
      min-height: 40px; 
      margin: 10px auto;
      display: block;
    }
    &>span{
      font-size: 30px;
      color: #777;
    }
  }
  .workRange {
    position: absolute;
    min-width: 35px;
    min-height: 35px;
    border-radius: 50%;
    background-color: red;
    top: 0;
    right: 13%;
    color: #fff !important;
    font-size: 25px;
    transform: scale(0.8);
    padding: 5px 3px 0 3px;
  }
</style>

<template>
  <footer class="footer" @touchmove.prevent>
    <div class="flex_bottom" @click="changeHomeFooter(foot, index)" v-for="(foot, index) in footerNavList" :key="index">
      <img :src="foot.code ? foot.beforeImg : foot.afterImg" :alt="foot.description">
      <span v-if="newsStatus > 0 && index === 2" class="workRange">{{newsStatus}}</span>
      <span :style="{ 'color': !foot.code ? '#777' : '#3fb140' }">{{foot.description}}</span>
    </div>
  </footer>
  <!-- 底部切换TAB 页面 END -->
</template>
<script>
  import { mapMutations, mapState } from 'vuex'
  export default {
    name: 'homeFooter',
    data() {
      return {
        workRange: 0,
        home: 0,
        footerNavList: [{
            beforeImg: require('../assets/img/menu_shouye_select.png'),
            afterImg: require('../assets/img/menu_shouye_default.png'),
            description: '首页',
            pathName: '/',
            code: false
          },
          {
            beforeImg: require('../assets/img/menu_zuopinquan_select.png'),
            afterImg: require('../assets/img/menu_zuopinquan_default.png'),
            description: '作品圈',
            pathName: '/',
            // pathName: '/workCircle',
            code: false
          },
          {
            beforeImg: require('../assets/img/menu_zuoyequ_select.png'),
            afterImg: require('../assets/img/menu_zuoyequ_default.png'),
            description: '作业区',
            pathName: '/workArea/all/highestHeat',
            // pathName: '/',
            code: false
          },
          {
            beforeImg: require('../assets/img/menu_wode_select.png'),
            afterImg: require('../assets/img/menu_wode_default.png'),
            pathName: '/mine',
            description: '我的',
            code: false
          },
        ]
      }
    },
    methods: {
      ...mapMutations([
        'modifiedHomeFooterStatus'
      ]),
      changeHomeFooter(item, index){
        this.$router.push(item.pathName)
        this.modifiedHomeFooterStatus(index)
      }
    },
    computed: {
      ...mapState([
        'homeFooterStatus',
        'newsStatus'
      ])
    },
    watch: {
      homeFooterStatus(data){
        this.footerNavList[data].code = true
      }
    },
    mounted() {
      // 判断刷新后的Footer状态
      if(this.$route.fullPath.includes('/mine')){
        this.modifiedHomeFooterStatus(3)
      }else if(this.$route.fullPath.includes('/workArea')){
        this.modifiedHomeFooterStatus(2)
      }else{
        this.modifiedHomeFooterStatus(0)
      }
      this.footerNavList[this.homeFooterStatus].code = true
    }
  }
</script>
  


