<template>
  <div class="header">
    <div><img :src="back_img" alt="返回" class="back" @click="back"></div>
    <span>{{centerTitle}}</span>
  </div>
</template>
<script>
  export default {
    props: [
      'centerTitle',
      'rightTitle'
    ],
    data() {
      return {
        back_img: require('../assets/img/per_fh.png'),
      }
    },
    methods: {
      back() {
        this.$router.go(-1);
      },
    },
    mounted() {
    }
  }
</script>
<style lang="less" scoped>
  .header {
    height: 100px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 1;
    border-bottom: .0625rem solid rgba(0,0,0,.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    div {
      flex: 1;
      padding-left: 20px;
      padding-top: 50px;
      height: 100px;
      vertical-align: middle;
      display: inline-block;
      text-align: left;
      img {
        width: 30px;
        vertical-align: middle;
      }
    }
    span {
      text-align: center;
      padding-right: 100px;
      flex: 9;
      vertical-align: middle;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 35px;
    }
  } // 头部
</style>


