<template>
  <swiper :options="emojiSwiper" ref="emojiSwipers">
    <swiperSlide v-for="(emoji,index) in emojiList" class="all-emoji-list" :key="index">
      <div v-for="(item,index_) in emoji" class="emoji-list" :key="index_" @click.prevent.stop="writeEmoji(item)">
        <img :src="item.url" :alt="item.title">
      </div>
    </swiperSlide>
  </swiper>
</template>
<script>
  import {
    swiper,
    swiperSlide
  } from 'vue-awesome-swiper'
  import emoji from '../utils/emojiJson'
  export default {
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {
        emojiList: new Array(),
        emojiSwiper: {
          slidesPerView: 1,
          observer:true, // 修改swiper自己或子元素时，自动初始化swiper
          observeParents:true, // 修改swiper的父元素时，自动初始化swiper
          onSlideChanged: swiper => {
            this.page = swiper.realIndex + 1;
            this.index = swiper.realIndex;
          },
        },
      }
    },
    computed: {
      swiper() {
        return this.$refs.emojiSwipers.swiper
      },
    },
    mounted() {
      this.emojiList = emoji
      // 解决在其他组件存活问题
      let that = this
      function interval() {
        if (that.swiper.loop) {
          that.swiper.slideNext()
        }
        setTimeout(function() {
          interval()
        }, 2000);
      }
      interval()
    },
    methods: {
      writeEmoji(emojiInfo) {
        this.$emit('getEmoji', `[${emojiInfo.title}]`)
      },
    },
  }
</script>
<style lang="less" scoped>
.swiper-container{
  position: fixed;
  bottom: 100px;
  width: 100%;
  background-color: #fff;
  padding-top: 10px !important;
  &>.swiper-wrapper{
    &>.swiper-slide{
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-wrap: wrap;
      &>.emoji-list{
        width: 15%;
        &>img{
          width: 70px;
          height: 70px;
        }
      }
    }
  }
}
</style>



