<style lang="less" scoped>
.valid-warp {
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  z-index:  89;
  width: 100%;
  max-width: 1040px;
  top: 0;
  & > .valid-container {
    background-image: url("https://image.dapengjiaoyu.com/2018/08/jl1p5kie.png");
    // background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    width: 85%;
    height: 100vh;
    margin: 0 auto;
    background-position: center 52px;
    padding-top: 300px;
    position: relative;
    & > .closeModal{
      background-image: url('../assets/img/guanbi.png');
      display: block;
      width: 70px;
      height: 70px;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: 0 0;
      position: absolute;
      top: 135px;
      right: 0;
    }
    & > .backImg{
      background-image: url('../assets/img/per_fh.png');
      display: block;
      width: 40px;
      height: 60px;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: 0 0;
      position: absolute;
      top: 160px;
      left: 30px;
    }
    &>p{
      width: 70%;
      margin: 0 auto;
    }
    &>p:nth-child(1){
      text-align: center;
    }
    &>p:nth-child(2){
      margin: 0 auto;
      padding: 15px 20px;
      background-color: #fff;
      background-size: cover;
      background-repeat: no-repeat;
      -moz-box-shadow: 2px 2px 5px #888;
      -webkit-box-shadow: 2px 2px 5px #666;
      box-shadow: 2px 2px 5px #888;
      border-radius: 10px;
      position: relative;
      &::after{
        content: '';
        display: block;
        position: absolute;
        bottom: -19px;
        z-index: 89;
        left: 50%;
        transform: translateX(-50%);
        width: 40px;
        height: 30px;
        background-image: url('../assets/img/phone_bd_sj.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
      }
      &>span{
        display: inline-block;
        width: 100%;
        text-align: center;
        &:nth-child(3){
          color: #44b336;
          font-size: 18px;
          margin: 5px 0;
        }
      }
    }
    &>p:nth-child(3){
      margin-top: 20px;
    }
    &>p:nth-child(3),&>p:nth-child(4){
      width: 80%;
      text-align: center;
      &>span{
        background-image: url('../assets/img/phone.png');
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        width: 50px;
        height: 50px;
        display: inline-block;
        vertical-align: middle;
      }
      &>input{
        margin-left: 10px;
        vertical-align: middle;
        height: 70px;
        border: none;
        border: 0;
        border-bottom: 1px  solid #aaa;
        outline: none;
      }
    }

    &>p:nth-child(4){
      margin-top: 10px;
      &>span{
        background-image: url('../assets/img/yzm.png');
      }
      &>input{
        width: 205px;
        background-color: transparent;
      }
      &>button{
        border: none;
        border: 0;
        outline: none;
        width: 150px;
        height: 55px;
        color: #44b336;
        font-size: 20px;
        background-color: transparent;
      }
    }
    &>button{
      display: block;
      margin: 60px auto 0 auto;
      width: 250px;
      height: 60px;
      background-color: #44b336;
      color: #fff;
      border: none;
      border: 0;
      outline: none;
      border-radius: 25px;
      font-size: 25px;
    }
  }
}
.mobileNum{
  font-size: 40px !important;
  display: inline-block;
}
</style>

<template>
  <div class="valid-warp">
    <div class="valid-container">
        <p></p>
        <p>
            <span>同学你好</span>
            <span>您当前登录的手机号码是</span>
            <span class="mobileNum">{{mobileNum}}</span>
            <span>1.如果此号码是您当前正在使用的手机号码，请再次输入此号码，实现绑定！</span><br>
            <span>2.如果此号码不正确或已经更换，请输入您正在使用的手机号码，绑定新号码！</span>
        </p>
        <p class="unvalid-mobile-wrap"><span></span><input v-model="validMobileNum" type="text" id="unvalid-mobile" class="unvalid-mobile" placeholder="请输入手机号"></p>
        <p class="unvalid-yzm-wrap"><span></span><input v-model="validMobileCode" placeholder="请输入验证码" type="text" id="unvalid-yzm" class="unvalid-yzm"><button @click="getValidCode" :disabled="validCodeDisabled">{{getValidateCode}}</button></p>
        <button @click="bindSubmitEvent">提交绑定</button>
        <span class="backImg" @click="backLoginEvent"></span>
        <span class="closeModal" @click="closeValidEvent"></span>
    </div>
  </div>
</template>
<script>
import { createNamespacedHelpers } from 'vuex'
const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
export default {
  components: {
    
  },
  computed: {
    ...mapState({
      VALID_USER_OBJECT: state => state.VALID_USER_OBJECT
    })
  },
  props: [
    'mobileNum'
  ],
  data () {
    return {
      validMobileNum: null, // 要验证的手机号码
      validMobileCode: null, // 接收到的手机验证码
      getValidateCode: '获取验证码',
      countDown: 60, // 倒计时
      validCodeDisabled: false, // 是否可点击
    }
  },

  methods: {
    ...mapActions([
      'MOBILE_CODE',
      'CHECK_REALMOBILE'
    ]),
    ...mapMutations([
      'BIND_USER'
    ]),
    bindSubmitEvent(){
      if(!this.validMobileNum){
        this.$Toast({
          message: '请输入手机号',
          position: 'middle',
          duration: 1000
        })
        return
      }
      if(!this.validMobileCode){
        this.$Toast({
          message: '请输入验证码',
          position: 'middle',
          duration: 1000
        })
        return
      }

      this.$Indicator.open({
        text: '操作中...',
        spinnerType: 'fading-circle'
      })
      
      this.CHECK_REALMOBILE({
        userId: this.VALID_USER_OBJECT.user.userId,
        mobile: this.validMobileNum,
        code: this.validMobileCode
      }).then(data => {
        if(data.data && data.data.status === 0){
          this.$Toast({
            message: data.data.content,
            position: 'middle',
            duration: 1000
          })
        }else{
          this.$Indicator.close()
          this.BIND_USER(data.data.content)
          localStorage.setItem('dapeng', data.data.token)
          this.$Toast({
            message: '绑定成功',
            position: 'middle',
            duration: 1000
          })
          this.$store.commit('cleanAllStatus')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getValidCode(){
      
      // 准备请求
      let params = {
        phone: this.validMobileNum,
        oldphone: this.mobileNum,
        type: 'REAL_PHONE'
      }
      if(!this.validMobileNum){
        this.$Toast({
          message: '请填写要绑定的手机号',
          position: 'middle',
          duration: 1000
        })
        return
      }
      this.MOBILE_CODE(params).then(data => {
        if(data.data.status === 'failure'){
          this.$Toast({
            message: data.data.result,
            position: 'middle',
            duration: 1000
          })
          return
        }
        this.$Toast({
          message: '已发送验证码',
          position: 'middle',
          duration: 1000
        })
        this.validCodeDisabled = true
        let timer = setInterval(()=>{
          this.countDown--
          this.getValidateCode = this.countDown
          if(this.countDown === 0){
            this.validCodeDisabled = false
            this.getValidateCode = '获取验证码'
            this.countDown = 60
            clearInterval(timer)
          }
        }, 1000)

      }).catch(err => {
        console.log(err)
      })


    },
    closeValidEvent(){
      this.$emit('closeValidEvent', 0)
    },
    backLoginEvent(){
      this.$emit('backLoginEvent', 0)
    }
  }
}
</script>



